import React, { FC } from "react";

import { Icon } from "../Icon/Icon";
import {
	colors,
} from "../styleguide";
import Typography from "../Typography/Typography";
import uuid from "../utils/uuid";

import { CloseIconWrapper, IconWrapper, StyledTag } from "./Tag.styles";
import { TagProps } from "./Tag.types";

const Tag: FC<TagProps> = ({
	onDelete,
	id = uuid(),
	dataQA,
	icon,
	text,
	className,
	m,
	mt,
	mb,
	mr,
	ml,
	isDisabled,
}) => {
	const handleClick = (event: React.MouseEvent) => {
		if (onDelete) {
			onDelete(event);
		}
	};

	const handleKeyboard = (event: React.KeyboardEvent) => {
		if (onDelete && (event.key === "Enter" || event.key === "Backspace" || event.key === " ")) {
			onDelete(event);
		}
	};

	let deletableProps;

	if (isDisabled) {
		deletableProps = {
			tabIndex: -1,
			isDisabled,
		};
	} else {
		deletableProps = {
			tabIndex: 0,
			onClick: handleClick,
			onKeyDown: handleKeyboard,
		};
	}

	return (
		<StyledTag
			id={id}
			data-qa={dataQA}
			className={className}
			m={m}
			mt={mt}
			mb={mb}
			mr={mr}
			ml={ml}
			{...(onDelete && deletableProps)}
		>
			{ icon && <IconWrapper><Icon name={icon} size={12} strokeWidth="3" /></IconWrapper> }
			<Typography
				dataQA={`${dataQA}-typography`}
				variant="bodyCopySmall"
				component="span"
				color={colors.black}
			>
				{text}
			</Typography>
			{ onDelete && (
				<CloseIconWrapper title="remove" data-qa={`${dataQA}-close-icon-wrapper`}>
					<Icon name="X" size={12} strokeWidth="3" />
				</CloseIconWrapper>
			) }
		</StyledTag>
	);
};

Tag.displayName = "Tag";
export default Tag;
